<template>
  <div id="app">
    <!-- Navbar Start -->
    <b-navbar id="navbar" fixed="top">
      <b-container>
        <b-navbar-brand href="#" id="navbarBrand" to="/">
          SEARCH <b>LAB</b>
        </b-navbar-brand>
        <b-navbar-nav class="ml-auto" id="navbarRight">
          <b-nav-item to="note">NOTE</b-nav-item>
        </b-navbar-nav>
      </b-container> 
    </b-navbar>
    <!-- Navbar End -->
    <router-view/>
  </div>
</template>
<script>
import Vue from 'vue';
import VueTypedJs from 'vue-typed-js';
import { db, logInfo } from './firebase.js';
Vue.use(VueTypedJs);
export default {
  name: 'app',
  data: function(){
    return {
      permission: false,
      signup: false,
      showUser: true
    }
  },
  methods: {
   
  }
}
</script>
<style>
/** Global Fonts */
* {
  font-family: 'Montserrat', sans-serif;
}
/** Hide Scroll Bar */
::-webkit-scrollbar { 
  display: none; 
}

/** Navbar Design Start */
#navbar{
  background: linear-gradient(to right, #8A2BE2 -1%, springgreen 100%);
  border-bottom: 1px solid #d2d2d2;
}

#navbar #navbarBrand{
  text-transform: capitalize;
  font-size: 1rem;
  margin-left: 20px;
  color: #fff;
}
#navbarBrand #navTypeJs{
  display: inline-block;
}

#navbar #navbarRight{
  margin-right: 20px;
}
#navbarRight #govtLink, #privateLink, #newLink{
  position: relative;
  margin-left: 15px;
  display: inline-block;
  font-size: 1rem;
  line-height: inherit;
  white-space: nowrap;
}
.navbar-light .navbar-nav .nav-link{
  color: #fff !important
}
/** Navbar Design End */


/** App Media Only */
@media (min-width: 1200px){
  #navbar{
    padding: 20px 15px;
  }
  #contentSection{
    margin-top: 80px;
  }
}
@media screen and (max-width: 991px) {
  /** Navbar */
  #navbar #navbarRight{
    margin-right: 0;
  }
  #navbar{
    padding: 5px 15px;
  }
  #contentSection{
    margin-top: 80px;
  }
}
@media screen and (max-width: 767px) {
  /** Navbar */
  #navbar #navbarBrand{
    margin-left: 0;
  }
  #navbar{
    padding: 5px 15px;
  }
  #contentSection{
    margin-top: 50px;
  }
}
@media screen and (max-width: 479px) {
  #navbar{
    padding: 5px 15px;
  }
  #contentSection{
    margin-top: 50px;
  }
}
/** App Media End */

/** LOADER START */
/** Loader Custom */
.lds-ellipsis, .lds-ring{
  z-index: 1;
  position:absolute;
  left:0;
  right:0;
  margin-left:auto;
  margin-right:auto;
  top: 150px;
}

/** Loader Default */
.lds-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}
#loaderText{
  position: relative; 
  top: 100px; 
  color: #6c757d;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 2px solid #6c757d;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #6c757d transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/** LOADER END */
</style>



