<template>
  <div>
    <div class="lds-ring" v-if="loader"><span id="loaderText">Loading</span><div></div><div></div><div></div><div></div></div>
    <div v-if="dataLoadSignal" id="contentSection">
      <b-container>
        <b-row>
          <b-col md="4" sm="12">
            <b-card class="text-center" id="titleCard">
              SEARCH
            </b-card>
            <div>
              <b-card>
                <b-form-input type="text" placeholder="SEARCH" v-model="search"></b-form-input>
              </b-card>
            </div>
            <b-card class="text-center" id="titleCard">
              INSERT NEW
            </b-card>
            <div>
              <b-card>
                <b-form @submit="onSubmit" @reset="onReset">
                  <b-form-group>
                    <b-form-input type="text" placeholder="SEARCH KEY" v-model="question"></b-form-input>
                  </b-form-group>
                  <b-form-group>
                    <b-form-textarea placeholder="ANSWERE" rows="3" max-rows="6" v-model="answere"></b-form-textarea>
                  </b-form-group>
                  <b-form-group>
                    <b-form-input type="text" placeholder="REF. LINK" v-model="link"></b-form-input>
                  </b-form-group>
                  <b-row>
                    <b-col cols="6">
                      <b-button size="sm" type="submit" variant="success">SAVE</b-button>
                    </b-col>
                    <b-col cols="6" class="text-right">
                      <b-button size="sm" type="reset" variant="danger">CLEAR</b-button>
                    </b-col>
                  </b-row>
                </b-form>
              </b-card>
            </div>
          </b-col>
          <b-col md="8" sm="12">
            <b-card class="text-center" id="titleCard">
              RESULT
            </b-card>
            <div v-for="item in jobRetriveData" :key="item">
              <b-card v-if="search == ''">
                <div class="question-card">{{ item.question }}</div>
                <div class="answere-card">{{ item.answere }}</div>
                <div class="link-card" v-if="item.link != ''"><a :href="item.link" target="_blank">REF. LINK : {{ item.link }}</a></div>
              </b-card>
              <b-card v-if="search != '' && item.question == search">
                <div class="question-card">{{ item.question }}</div>
                <div class="answere-card">{{ item.answere }}</div>
                <div class="link-card" v-if="item.link != ''"><a :href="item.link" target="_blank">REF. LINK : {{ item.link }}</a></div>
              </b-card>
            </div>
          </b-col>
        </b-row>
      </b-container>
    </div>
  </div>
</template>

<script>
import { db, webdoc } from '../firebase.js';
export default {
  data() {
    return {
      jobRetriveData: [],
      logData: [],
      loader: true,
      dataLoadSignal: false,
      question: '',
      answere: '',
      link: '',
      search: ''
    }
  },
  firestore(){
    return {
      jobRetriveData: db.collection('web-doc').orderBy('createdAt','desc'),
    }
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      webdoc.add({
        question: this.question,
        answere: this.answere,
        link: this.link,
        createdAt: new Date(),
      })
      .then(function(docRef){
        console.log("Document written with ID:" + docRef.id);
        alert("Success! ID : " + docRef.id);
      })
      .catch(function(error){
        console.error("Error adding document:" + error);
        alert("Faild! - " + error);
      });
    },
    onReset(evt) {
      evt.preventDefault();
      // Reset our form values
      this.question = '',
      this.answere = '',
      this.link = ''
    }
  },
  watch: {
    'jobRetriveData': function(){
      if(this.jobRetriveData.length > 0){
        this.loader = false;
        this.dataLoadSignal = true;
      }
    }
  }
}
</script>
<style scoped>
#titleCard{
  background: linear-gradient(to right, #8A2BE2 -1%, springgreen 100%);
  color: #fff;
  border: 0px;
  margin: 15px 0;
}
#titleCard div{
  padding: 10px;
}
.card{
  border: 1px solid #8A2BE2;
  color: #8A2BE2;
  margin-bottom: 15px;
}
.question-card{
  font-size: 30px;
  margin-top: -10px;
  text-transform: uppercase;
}
.answere-card{
  font-size: 18px;
}
.link-card{
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  margin-top: 5px;
}
</style>



